.mskill {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  background: url('../../../../assets/img/bg/skill.jpg') no-repeat;
  background-size: cover;
  z-index: -999;
}
.mskill:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #eee;
  opacity: 0.8;
  z-index: -99;
}
.mskill__title {
  padding: 10px 0;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.mskill__title .title__line {
  display: inline-block;
  width: 15%;
  border-top: 1px solid #555 ;
}
.mskill__title .title__text {
  padding: 0 10px;
  vertical-align: -webkit-baseline-middle;
  font-size: 22px;
  color: #686868;
}
.mskill__container {
  flex: 1;
}
.mskill__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
}
.mskill__list .list__card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(33.33%);
  height: calc(33.33%);
  perspective: 1000px;
}
.mskill__list .list__card:hover .card__front {
  transform: rotateY(180deg);
}
.mskill__list .list__card:nth-child(even):hover .card__front {
  transform: rotateY(-180deg);
}
.mskill__list .list__card:hover .card__back {
  transform: rotateY(0deg);
  z-index: 1;
}
.mskill__list .list__card:nth-child(even):hover .card__back {
  transform: rotateY(0deg);
  z-index: 1;
}
.mskill__list .list__card .card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 60%;
  width: 80%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transition: ease-in-out 600ms;
  border-radius: 6px;
}
.mskill__list .list__card .card__front {
  background-position: center;
  background-size: cover;
  overflow: hidden;
}
.mskill__list .list__card .card__back {
  display: flex;
  flex-direction: column;
  padding: 0 5px;
  background: #fff;
  transform: rotateY(-180deg);
}
.mskill__list .list__card .card__back .back__title {
  padding-bottom: 10px;
  text-shadow: 0 1px 2px #555;
  font-size: 18px;
  font-weight: 500;
}
.mskill__list .list__card .card__back .back__intro {
  font-size: 14px;
}
